import React, {useState, useEffect } from 'react'
import unsplash01 from "../images/unsplash01.jpg";
import unsplash02 from "../images/unsplash02.jpg";
import unsplash03 from "../images/unsplash03.jpg";

function Head() {
  const [ imgIndex, setImgIndex] = useState(0);


  let currentindex= 0;

  useEffect(() => {
    const interval = setInterval(() => {
      currentindex += 1;
      if(currentindex > 2){
        currentindex = 0;
      }
      setImgIndex(currentindex);
    }, 7000);
    return () => clearInterval(interval)
  }, [])

  const imagelist = [ unsplash01, unsplash02, unsplash03 ];

 
  const textAnime1 = imgIndex === 0?"visible":null;
  const textAnime2 = imgIndex === 1?"visible":null;
  const textAnime3 = imgIndex === 2?"visible":null;

  const buttonAnime1 = imgIndex === 0?"button":null;
  const buttonAnime2 = imgIndex === 1?"button":null;
  const buttonAnime3 = imgIndex === 2?"button":null;


  return (
    <div className="head-wrapper" >
      <div className="head1 head">
        <img src={imagelist[imgIndex]} alt="unsplash"/>
        <div className="text">
          <h2 className={textAnime1}> superior knwoledge does not grow on trees</h2>
          <h2 className={textAnime2}>dont believe it can be done</h2>
          <h2 className={textAnime3}>success start from who you hire</h2>
          <button className={buttonAnime1}>learn more</button>
          <button className={buttonAnime2}>learn more</button>
          <button className={buttonAnime3}>learn more</button>
        </div>  
      </div>
     </div>
  )
}

export default Head
